<template>
  <div class="wrapper">
      <div class="password-copy" ref="copyPassword">пароль скопирован</div>

    <div class="password">
      <div class="container">
        <password-form @copyPassword="copyPassword"></password-form>
      </div>
    </div>
  </div>
</template>
<script>
import { ref } from '@vue/reactivity';
import passwordForm from "./components/password-form.vue";
export default {
  components: { passwordForm },
  data() {
    return {};
  },
  methods: {
    copyPassword() {
      const copyPassword = this.$refs.copyPassword;

      copyPassword.classList.add('active')
      setTimeout(() => {
        copyPassword.classList.remove('active');
      }, 800);
    }
  }
};
</script>

